/* src/pages/Notice.module.css */

.noticeContainer {
  padding: 20px;
}

.title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #2db400;
}

.noticeList {
  width: 100%;
  border-collapse: collapse; /* 테두리 겹침 */
  table-layout: fixed; /* 테이블 열 너비를 고정 */
}

.noticeList th, .noticeList td {
  border: 1px solid #ddd; /* 셀 테두리 */
  padding: 10px; /* 셀 안 여백 */
  text-align: center; /* 셀 내용 가운데 정렬 */
  vertical-align: middle; /* 세로 중앙 정렬 */
  word-wrap: break-word; /* 긴 텍스트 줄바꿈 */
}

.noticeList th {
  background-color: #f2f2f2; /* 테이블 헤더 배경색 */
  color: #333;
  font-weight: bold;
}

.noticeList tr:nth-child(even) {
  background-color: #f9f9f9; /* 짝수 행 배경색 */
}

.noticeList tr:hover {
  background-color: #f1f1f1; /* 마우스 오버 시 배경색 */
}

.noticeTitle {
  color: blue;
  cursor: pointer;
  text-decoration: underline;
}

.noticeItem {
  font-size: 1rem;
}

.loading {
  text-align: center;
  font-size: 1.5rem;
  color: #555;
}

.error {
  text-align: center;
  font-size: 1.2rem;
  color: red;
}

  