.container {
  display: flex;              /* Flexbox 레이아웃 사용 */
  justify-content: center;    /* 가로 중앙 정렬 */
  align-items: center;        /* 세로 중앙 정렬 */
  height: 100vh;              /* 전체 뷰포트 높이 */
  padding: 20px;
  box-sizing: border-box;
  background-color: #f9f9f9;  /* 전체 배경색 */
  text-align: center;
}

.title {
  font-size: 2rem;
  color: #2db400;
  margin-bottom: 20px;
}

.description {
  font-size: 1.2rem;
  color: #555;
}

.content {
  display: flex;
  flex-direction: row;        /* 가로로 섹션 정렬 */
  gap: 50px;                  /* 섹션 간격 */
  width: 90%;                 /* 전체 컨텐츠 너비 */
  background-color: #fff;     /* 내용 배경색 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
  border-radius: 8px;         /* 테두리 둥글게 */
  padding: 20px;
}

.table {
  width: 100%;              /* 테이블 너비를 100%로 설정 */
  table-layout: fixed;      /* 열 너비를 고정 */
  border-collapse: collapse; /* 테두리 겹침 */
}

.table th, .table td {
  border: 1px solid #ddd;
  padding: 10px;            /* 셀 내부 여백 */
  text-align: center;       /* 내용 가운데 정렬 */
  vertical-align: middle;   /* 세로 중앙 정렬 */
  word-wrap: break-word;    /* 긴 텍스트 줄바꿈 */
}

.table th {
  background-color: #f4f4f4;
  font-weight: bold;
}

.table tr:nth-child(even) {
  background-color: #f9f9f9; /* 짝수 행 배경색 */
}

.table tr:hover {
  background-color: #f1f1f1; /* 마우스 오버 시 배경색 */
}

.section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;      /* 테이블을 가운데 정렬 */
  justify-content: center;
}

h2 {
  margin-bottom: 10px;      /* 섹션 제목 여백 */
  color: #333;
}

.link {
  color: #007bff;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}
