/* src/pages/board/BoardDetail.module.css */

.boardDetailContainer {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.boardTable {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.boardTable th {
    background-color: #f4f4f4;
    text-align: left;
    padding: 10px;
    border: 1px solid #ddd;
}

.boardTable td {
    padding: 10px;
    border: 1px solid #ddd;
}

.buttonGroup {
    margin-top: 20px;
    text-align: right;
}

.buttonGroup button {
    margin-left: 10px;
    padding: 10px 15px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
}

.buttonGroup button:hover {
    background-color: #0056b3;
}

.replyContainer {
    margin-top: 20px;
}

.replyItem {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.replyMeta {
    font-size: 0.9em;
    color: #555;
    margin-top: 5px;
}

.replyIndented {
    margin-left: 20px;
    border-left: 3px solid #007bff;
    padding-left: 15px;
}
