/* src/pages/notice/NoticeDetail.module.css */

.noticeContainer {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto; /* 가운데 정렬 */
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-family: Arial, sans-serif;
  }
  
  h2 {
    text-align: center;
    color: #2db400;
    margin-bottom: 20px;
  }
  
  .noticeTable {
    width: 100%;
    border-collapse: collapse; /* 테이블 셀 테두리 겹치기 */
  }
  
  .noticeTable th,
  .noticeTable td {
    border: 1px solid #ddd; /* 셀 테두리 */
    padding: 7px; /* 셀 여백 */
    text-align: left; /* 왼쪽 정렬 */
    vertical-align: middle; /* 세로 중앙 정렬 */
  }
  
  .noticeTable th {
    background-color: #f2f2f2; /* 헤더 배경색 */
    color: #333;
    font-weight: bold;
    width: 170px;
    text-align: center;
  }
  
  .noticeTable tr:nth-child(even) {
    background-color: #f9f9f9; /* 짝수 행 배경색 */
  }
  
  .noticeTable tr:hover {
    background-color: #f1f1f1; /* 마우스 오버 시 배경색 */
  }
  
  .buttonGroup {
    display: flex;
    justify-content: center;
    gap: 10px; /* 버튼 간격 */
    margin-top: 20px;
  }
  
  button {
    background-color: #2db400;
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #238a00;
  }
  
  .loading {
    text-align: center;
    font-size: 1.5rem;
    color: #555;
  }
  
  .error {
    text-align: center;
    font-size: 1.2rem;
    color: red;
  }
  